.main-title {
    margin-bottom: 1em;
    font-size: 50px;
    line-height: 61px;
    font-weight: 400;
    letter-spacing: 0.055em;
    color: #4A484E;
    text-align: center;
    padding-bottom: 10px;
}

.login-container {
    width: 400px;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.container-button button {
    width: 80%;
    max-width: 200px;
    height: 50px;
    margin: 1em auto;
    padding:0.4em 0.4em;
}

.container-button {
    padding-top: 20px;
}
.container-button > p {
    margin-top: 1em;
    color: red;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.06em;
    text-align: center;
} 

@media (max-width: 500px) {
    .initial-view-injected-component, .login-container {
        max-width: 400px;
        width: 100%;
    }
}