.first-step-paragraph {
    font-weight: 400;
    font-size: 50px;
    color: #4A484E;
    letter-spacing: 0.055em;
}

label {
    color: #4A484E;
    padding-left: 10px;
}

/*PROGRESS-BAR*/

.progress-bar {
    display: flex;
    width: 100%;
    height: 14px;
    background: rgba(228, 177, 137, 0.7);
    border-radius: 5px;
}

.first-step {
    width: 50%;
    background-color: #DA1B36;
    border-radius: 5px;
}


/*FORM - basic setup*/

form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 500px;
}

.gender-selection {
    display: flex;
}

.gender-selection span {
    width: 2.5rem;
}

.name,
.birth-date,
.height,
.goal-weight,
.current-weight {
    background: #FFFFFF;
    border: 1px solid #4A484E;
    box-shadow: 2px 4px 4px rgba(74, 72, 78, 0.25);
    border-radius: 7px;
    padding: 15px;
    outline: none;
}

.current-values {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.div-align {
    width: 35px;
}

.name,
.birth-date {
    width: 100%
}

.height,
.current-weight {
    width: 48%;
}

.goal-weight {
    width: 48%;
    align-self: flex-start;
}

.first-step-form button {
    width: auto;
    height: auto;
    padding: 0.5em 1em;
    background-color: red;
    border-radius: 0.5em;
}